.itemDisplay {
  border: .3rem solid;
  margin: .38rem;
}

.twoStarWeapon {
  border-color: #72b872;
}

.threeStarWeapon {
  border-color: #3c71bb;
}

.cogs, .exp, .overclock, .constructShard, .fourStarEquipment, .fourStarWeapon {
  border-color: #cb68c2;
}

.aConstruct, .bConstruct, .bOrAConstruct, .fiveStarWeapon {
  border-color: #ff8d1c;
}

.sConstruct, .sixStarWeapon, .transcendant {
  border-color: #fb5824;
}

img.constructShard {
  background-blend-mode: difference;
  filter: brightness() invert(30%);
  background-position: calc(50% - 1px) calc(50% - 1px), calc(50% + 1px) calc(50% + 1px);
  background-size: cover;
  box-shadow: inset 0 0 0 1px #000;
}

@supports (filter: invert(1)) and (background-blend-mode: difference) {
  .img.constructShard {
    background-blend-mode: difference;
    filter: brightness(2) invert() grayscale();
    background-position: calc(50% - 1px) calc(50% - 1px), calc(50% + 1px) calc(50% + 1px);
    box-shadow: inset 0 0 0 1px #000;
  }
}

